/* Subscription Card Specific Styles */
.subscription-card {
    /* Inherits card styles from Dashboard.css */
}

.subscription-card h3 {
    /* Inherits h3 styles from Dashboard.css */
}

.subscription-details p {
    margin: 8px 0;
    font-size: 0.95rem;
    color: #444;
}

.subscription-details strong {
    color: #333;
}

.progress-bar-container {
    width: 100%;
    background-color: #e0e0e0;
    border-radius: 5px;
    height: 10px;
    overflow: hidden;
    margin-top: 15px;
    margin-bottom: 5px;
}

.progress-bar {
    height: 100%;
    background-color: #28a745; /* Green for progress */
    border-radius: 5px;
    transition: width 0.5s ease-in-out;
}

.progress-text {
    font-size: 0.85rem;
    color: #666;
    text-align: center;
    margin-bottom: 20px;
}

.service-packages-list {
    margin-top: 20px;
    border-top: 1px solid #eee;
    padding-top: 20px;
}

.service-packages-list h4 {
    margin-top: 0;
    margin-bottom: 15px;
    color: #333;
    font-size: 1.1rem;
}

.service-package-option {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px 15px;
    border: 1px solid #e0e0e0;
    border-radius: 8px;
    margin-bottom: 10px;
    background-color: #fdfdfd;
}

.service-package-option.active {
    border-color: #007bff;
    background-color: #e7f3ff;
}

.service-package-option span {
    font-size: 0.95rem;
    color: #333;
}

.service-package-option .button {
    padding: 8px 15px;
    font-size: 0.9rem;
}

